var React = require('react');

var Markdown = require('./Markdown');

var ConfigFieldLabel = React.createClass({
  render: function() {
    return (
      <div className="field field-type-label">
        <label>{this.props.title}</label>
        <Markdown
          className="input input-type-label"
          value={this.props.value} />
      </div>
    );
  },
});

module.exports = ConfigFieldLabel;
